/* --------------hero--------------------------hero--------------------hero------------ */
.hero {
  margin-top: 18px;
  background: url("../images/hero/image-back.jpg") center/cover no-repeat;
  overflow: hidden;
}
@media (max-width: 768px) {
  .hero {
    background: url("../images/hero/image-back.jpg") 15%/cover no-repeat;
  }
}
@media (max-width: 460px) {
  .hero {
    margin-top: 6.563vw;
    background: url("../images/hero/image-back.jpg") 52%/auto 135% no-repeat;
  }
}
.hero__wrapper {
  position: relative;
}
.hero__wrapper img {
  display: none;
}
.hero__wrapper::before, .hero__wrapper::after {
  content: "";
  position: absolute;
}
.hero__wrapper::before {
  top: 50%;
  right: 6%;
  transform: translate(0, -50%);
  width: 200%;
  height: calc(100% - 136px);
  background: #FFFFFF;
  opacity: 0.8;
}
@media (max-width: 768px) {
  .hero__wrapper::before {
    height: calc(100% - 17.708vw);
  }
}
@media (max-width: 520px) {
  .hero__wrapper::before {
    right: -50%;
  }
}
@media (max-width: 460px) {
  .hero__wrapper::before {
    height: calc(100% - 19.688vw);
  }
}
.hero__wrapper::after {
  overflow: hidden;
  top: 0;
  right: 0;
  min-height: 100%;
  width: 632px;
  transform: translate(44%, 0);
  background: url("../images/hero/img.png") center/cover no-repeat;
}
@media (max-width: 768px) {
  .hero__wrapper::after {
    transform: translate(48%, 0);
  }
}
@media (max-width: 768px) and (max-width: 520px) {
  .hero__wrapper::after {
    transform: translate(52%, 0);
  }
}
@media (max-width: 500px) {
  .hero__wrapper::after {
    display: none;
  }
}
