@font-face {
	font-family: 'Muller';
	src: url('../fonts/Muller/Muller.eot');
	src: local('Muller'), local('../fonts/Muller/Muller'),
		url('../fonts/Muller/Muller.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Muller/Muller.woff') format('woff'),
		url('../fonts/Muller/Muller.ttf') format('truetype');
	font-display: swap
}


